"use client";
import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";

/* ---------------------------- Types ---------------------------- */

type TicketStatus =
    | "Draft"
    | "Submitted"
    | "In Progress"
    | "On Hold"
    | "Resolved"
    | "Rejected";

type Priority = "Low" | "Medium" | "High" | "Critical";

interface User {
    id: string;
    name: string;
    groupId: string;
}

interface Group {
    id: string;
    name: string;
}

interface Asset {
    id: string;
    name: string;
}

interface Activity {
    status: TicketStatus;
    timestamp: string;
    user: string;
}

interface Ticket {
    id: number;
    summary: string;
    description: string;
    category: string;
    subcategory: string;
    priority: Priority;
    status: TicketStatus;
    assignmentGroup: string;
    assignedTo: string;
    caller: string;
    ciAsset: string;
    source: string;
    attachments: File[];
    activities: Activity[];
    createdAt: string;
}

/* ---------------------------- Mock Reference Data ---------------------------- */

const GROUPS: Group[] = [
    { id: "it", name: "IT Support" },
    { id: "hr", name: "HR Operations" },
];

const USERS: User[] = [
    { id: "u1", name: "Alice", groupId: "it" },
    { id: "u2", name: "Bob", groupId: "it" },
    { id: "u3", name: "Emma", groupId: "hr" },
];

const ASSETS: Asset[] = [
    { id: "laptop-01", name: "Laptop - Dell" },
    { id: "vpn-01", name: "VPN Access" },
];

const CATEGORIES: Record<string, string[]> = {
    Hardware: ["Laptop", "Desktop", "Printer"],
    Software: ["Email", "VPN", "ERP"],
    HR: ["Payroll", "Leave"],
};

/* ---------------------------- Component ---------------------------- */

export default function HelpDeskPage() {
    const [tickets, setTickets] = useState<Ticket[]>([]);
    const [open, setOpen] = useState(false);
    const [editing, setEditing] = useState<Ticket | null>(null);
    const [page, setPage] = useState(1);

    const PAGE_SIZE = 5;

    const emptyForm: Omit<Ticket, "id" | "activities" | "createdAt"> = {
        summary: "",
        description: "",
        category: "",
        subcategory: "",
        priority: "Low",
        status: "Draft",
        assignmentGroup: "",
        assignedTo: "",
        caller: "",
        ciAsset: "",
        source: "Web",
        attachments: [],
    };

    const [form, setForm] = useState(emptyForm);

    /* ---------------------------- Derived ---------------------------- */

    const filteredUsers = USERS.filter(
        u => u.groupId === form.assignmentGroup
    );

    const paginated = tickets.slice(
        (page - 1) * PAGE_SIZE,
        page * PAGE_SIZE
    );

    const totalPages = Math.ceil(tickets.length / PAGE_SIZE);

    /* ---------------------------- Actions ---------------------------- */

    const openNew = () => {
        setEditing(null);
        setForm(emptyForm);
        setOpen(true);
    };

    const openEdit = (t: Ticket) => {
        setEditing(t);
        setForm({ ...t });
        setOpen(true);
    };

    const saveTicket = () => {
        const now = new Date().toISOString();

        if (editing) {
            const statusChanged = editing.status !== form.status;
            setTickets(
                tickets.map(t =>
                    t.id === editing.id
                        ? {
                            ...t,
                            ...form,
                            activities: statusChanged
                                ? [...t.activities, { status: form.status, timestamp: now, user: form.caller }]
                                : t.activities,
                        }
                        : t
                )
            );
        } else {
            setTickets([
                {
                    id: tickets.length + 1,
                    ...form,
                    createdAt: now,
                    activities: [{ status: "Draft", timestamp: now, user: form.caller }],
                },
                ...tickets,
            ]);
        }
        setOpen(false);
    };

    /* ---------------------------- UI ---------------------------- */

    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Help Desk" />

            <div className="flex justify-end mb-4">
                <button
                    onClick={openNew}
                    className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                >
                    New Ticket
                </button>
            </div>

            {/* ---------------- Table ---------------- */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white">
                <Table>
                    <TableHeader className="bg-gray-50 border-b">
                        <TableRow>
                            <TableCell isHeader className="px-5 py-3">ID</TableCell>
                            <TableCell isHeader className="px-5 py-3">Summary</TableCell>
                            <TableCell isHeader className="px-5 py-3">Status</TableCell>
                            <TableCell isHeader className="px-5 py-3">Priority</TableCell>
                            <TableCell isHeader className="px-5 py-3">Group</TableCell>
                            <TableCell isHeader className="px-5 py-3">CI / Asset</TableCell>
                        </TableRow>
                    </TableHeader>

                    <TableBody className="divide-y">
                        {paginated.map((t) => (
                            <TableRow key={t.id} className="hover:bg-gray-50">

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer"
                                        onClick={() => openEdit(t)}
                                    >
                                        {t.id}
                                    </div>
                                </TableCell>

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer font-medium text-blue-600 hover:underline"
                                        onClick={() => openEdit(t)}
                                    >
                                        {t.summary}
                                    </div>
                                </TableCell>

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer"
                                        onClick={() => openEdit(t)}
                                    >
                                        <Badge
                                            size="sm"
                                            color={
                                                t.status === "Resolved"
                                                    ? "success"
                                                    : t.status === "Rejected"
                                                        ? "error"
                                                        : t.status === "Submitted"
                                                            ? "warning"
                                                            : t.status === "On Hold"
                                                                ? "info"
                                                                : "secondary"
                                            }
                                        >
                                            {t.status}
                                        </Badge>
                                    </div>
                                </TableCell>

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer"
                                        onClick={() => openEdit(t)}
                                    >
                                        {t.priority}
                                    </div>
                                </TableCell>

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer"
                                        onClick={() => openEdit(t)}
                                    >
                                        {t.assignmentGroup}
                                    </div>
                                </TableCell>

                                <TableCell className="px-5 py-4">
                                    <div
                                        className="cursor-pointer"
                                        onClick={() => openEdit(t)}
                                    >
                                        {t.ciAsset}
                                    </div>
                                </TableCell>

                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </div>

            {/* ---------------- Pagination ---------------- */}
            {totalPages > 1 && (
                <div className="flex justify-center gap-2 mt-4">
                    {[...Array(totalPages)].map((_, i) => (
                        <button
                            key={i}
                            onClick={() => setPage(i + 1)}
                            className={`px-3 py-1 border rounded ${page === i + 1 ? "bg-blue-600 text-white" : ""
                                }`}
                        >
                            {i + 1}
                        </button>
                    ))}
                </div>
            )}

            {/* ---------------- Modal ---------------- */}
            <Modal isOpen={open} onClose={() => setOpen(false)} className="max-w-3xl p-6">
                <h2 className="text-lg font-semibold mb-4">
                    {editing ? `Ticket #${editing.id}` : "New Ticket"}
                </h2>

                <div className="grid grid-cols-2 gap-4">
                    <input
                        className="border rounded px-3 py-2"
                        placeholder="Summary"
                        value={form.summary}
                        onChange={e => setForm({ ...form, summary: e.target.value })}
                    />

                    <select
                        className="border rounded px-3 py-2"
                        value={form.priority}
                        onChange={e => setForm({ ...form, priority: e.target.value as Priority })}
                    >
                        {["Low", "Medium", "High", "Critical"].map(p => (
                            <option key={p}>{p}</option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.category}
                        onChange={e =>
                            setForm({ ...form, category: e.target.value, subcategory: "" })
                        }
                    >
                        <option value="">Category</option>
                        {Object.keys(CATEGORIES).map(c => (
                            <option key={c}>{c}</option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.subcategory}
                        onChange={e => setForm({ ...form, subcategory: e.target.value })}
                    >
                        <option value="">Subcategory</option>
                        {CATEGORIES[form.category]?.map(sc => (
                            <option key={sc}>{sc}</option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.assignmentGroup}
                        onChange={e =>
                            setForm({ ...form, assignmentGroup: e.target.value, assignedTo: "" })
                        }
                    >
                        <option value="">Assignment Group</option>
                        {GROUPS.map(g => (
                            <option key={g.id} value={g.id}>
                                {g.name}
                            </option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.assignedTo}
                        onChange={e => setForm({ ...form, assignedTo: e.target.value })}
                    >
                        <option value="">Assigned To</option>
                        {filteredUsers.map(u => (
                            <option key={u.id} value={u.name}>
                                {u.name}
                            </option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.ciAsset}
                        onChange={e => setForm({ ...form, ciAsset: e.target.value })}
                    >
                        <option value="">CI / Asset</option>
                        {ASSETS.map(a => (
                            <option key={a.id}>{a.name}</option>
                        ))}
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={form.source}
                        onChange={e => setForm({ ...form, source: e.target.value })}
                    >
                        {["Web", "Chat", "Integration", "Email"].map(s => (
                            <option key={s}>{s}</option>
                        ))}
                    </select>
                </div>

                <textarea
                    className="border rounded w-full mt-4 p-3"
                    placeholder="Description"
                    value={form.description}
                    onChange={e => setForm({ ...form, description: e.target.value })}
                />

                <div className="flex justify-end mt-4 gap-2">
                    <button className="border px-4 py-2 rounded" onClick={() => setOpen(false)}>
                        Cancel
                    </button>
                    <button className="bg-blue-600 text-white px-4 py-2 rounded" onClick={saveTicket}>
                        Save
                    </button>
                </div>
            </Modal>
        </div>
    );
}
